<template>
  <div>
    <div class="left-content" v-loading="loading">

      <div class="list-item" v-for="item in articles" :key="item.id">
        <img class="pic" :src="`${imgUrl}image/article/feature/${item.feature}`"/>
        <div class="article-info">
          <router-link class="title" v-html="item.title" :to="{path:'/detail',query:{id:item.id}}" />
          <p class="desc" v-text="item.content.match(reg)?item.content.match(reg).join(''):item.content"></p>
        </div>
      </div>

      <!--分页组件-->
      <el-pagination
        v-if="!ifSearch"
        background
        layout="prev, pager, next"
        :page-count="totalPage"
        @current-change="getAriticle">
      </el-pagination>
    </div>
    <img class="fixed-img" :src="`${imgUrl}image/article/feature/28.jpg`"/>
  </div>
</template>

<script>
  export default {
    name: "Home",
    data() {
      return {
        imgUrl: process.env.BASE_URL,
        totalPage:1,
        loading: true,
        reg: /[\u4e00-\u9fa5]/g
      }
    },
    computed: {
      ifSearch(){
        return this.$store.getters.getIfSearch
      },
      articles(){
        return this.$store.getters.getArticles
      }
    },
    created(){
      //获取首页数据
      this.$axios.get('/blog/api/getArticle?page=1&isHome=true').then((res) => {
        this.totalPage = res.data.totalpage
        this.$store.dispatch('changeArticles',res.data.articles)
        this.$store.dispatch('changeIfSearch', false)
        this.loading = false
      })
    },
    methods:{
      getAriticle(page){
        this.loading = true
        this.$axios.get('/blog/api/getArticle?page='+page).then((res) => {
          this.$store.dispatch('changeArticles',res.data)
          this.loading = false
        })
      }
    }
  }
</script>

<style scoped>
  .left-content {
    padding-top: 30px;
    padding-right: 30px;
    width: 70%;
    height: 400px;
    float: left;
    box-sizing: border-box;
  }

  .list-item {
    overflow: hidden;
    margin-bottom: 10px;
    border-bottom: 1px solid #dcdcdc;
    height: 105px;
    box-sizing: border-box;
  }

  .title {
    line-height: 27px;
    font-size: 18px;
    font-weight: 700;
    color: #333;
    cursor: pointer;
    text-decoration: none;
  }

  .desc {
    line-height: 24px;
    font-size: 13px;
    color: #999;
  }

  .pic {
    display: block;
    width: 16%;
    height: 100px;
    float: right;
    border-radius: 10px;
  }

  .article-info {
    float: left;
    width: 80%;
  }

  .fixed-img {
    width: 300px;
    height: 500px;
    position: fixed;
    top: 100px;
  }

</style>
